import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home/Home'
import ShopInspection from '@/components/ShopInspection/ShopInspection'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
     
    },
    {
      name:'shopPatrol',
      path:'/shopPatrol',
      component: ShopInspection
    }
   
  ]
})



